



















.edit {
				display: block;
				width: 80%;
				height: 150px;
				font-size: 40px;
				line-height: 85px;
				margin: 80px auto 30px;
				-webkit-box-sizing: border-box;
				        box-sizing: border-box;
				padding-left: 4px;
				border-radius: 4px;
				border: 1px solid #ccc;
				outline: 0;
				-webkit-box-shadow: 0 0 5px #ccc;
				        box-shadow: 0 0 5px #ccc;
}
.focus{
				-webkit-box-shadow: 0 0 50px #BF5;
				        box-shadow: 0 0 50px #BF5;
}
.list {
				margin: 115px auto 0;
				width: 80%;
}
.unit {
				margin-top: 20px;
				font-size: 30px;
				position: relative;
				padding: 10px 0;
				border-bottom: 1px solid #efefef;
}
.unit:last-child {
				border-bottom: 0;
}
.checkbox{
				width: 30px;
				height: 30px;
				vertical-align: middle;
}
span{
				  vertical-align: middle;
				  word-wrap: break-word;
}
.finish{
  text-decoration: line-through;
  color: #ccc;
}
.del{
display: block;
width: 90px;
height: 30px;
line-height: 30px;
  /*transform: translateY(16px);*/
  background: red;
  text-decoration: none;
  position: absolute;
  right:0;
  font-size: 25px;
  border: 0;
  outline: 0;
  border-radius: 20px;
  color: #fff;
  font-weight: bold;
}
.empty{
  font-size: 50px;
  color: #ccc;
  text-align: center;
  padding: 50px 0;
}
